<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">
        首页
      </el-breadcrumb-item>
      <el-breadcrumb-item>相册管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <ul class="albumlist">
        <li
          v-for="item in userlist"
          :key="item.userid"
          @click.prevent="getalbumlist(item)"
        >
          <div class="albumlist-img">
            <el-image
              src="../assets/img/1.jpg"
              width="164px"
              height="164px"
            >
              <div
                slot="error"
                class="image-slot"
              >
                <i class="el-icon-picture-outline" />
              </div>
            </el-image>
          </div>
          <div class="albumlist-b">
            {{ item.username || '用户名' }}
          </div>
        </li>
      </ul>
    </el-card>
    <el-dialog
      center
      :visible.sync="dialogVisible1"
      :before-close="handleClose1"
      title="相册管理"
      width="85%"
    >
      <ul class="albumlist albumlist1">
        <li
          v-for="item in albumlist"
          :key="item.albumid"
          @click.stop="getphotolist(item)"
        >
          <div class="albumlist-img">
            <el-image
              :src="item.aPhoto ? dfUrl + item.aPhoto.photoaddress : ''"
              width="164px"
              height="164px"
            >
              <div
                slot="error"
                class="image-slot"
              >
                <i class="el-icon-picture-outline" />
              </div>
            </el-image>
          </div>
          <div
            class="albumlist1-b"
            @click.stop=""
          >
            <span>{{ item.albumname || '相册名' }}</span>
            <el-switch
              v-model="item.albumtype"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="active"
              :inactive-value="inactive"
              @change="statusChange(item)"
            />
          </div>
        </li>
      </ul>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible1 = false">关 闭</el-button>
      </span>
    </el-dialog>
    <el-dialog
      center
      :visible.sync="dialogVisible2"
      :before-close="handleClose2"
      title="相片管理"
      width="85%"
    >
      <ul class="albumlist albumlist1">
        <li
          v-for="item in photolist"
          :key="item.photoid"
        >
          <div class="albumlist-img">
            <el-image
              :src="dfUrl + item.photoaddress"
              width="164px"
              height="164px"
              :preview-src-list="srcList"
            >
              <div
                slot="error"
                class="image-slot"
              >
                <i class="el-icon-picture-outline" />
              </div>
            </el-image>
          </div>
          <div class="albumlist-b">
            <el-switch
              v-model="item.photoban"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="active"
              :inactive-value="inactive"
              @change="statusChange(item)"
            />
          </div>
        </li>
      </ul>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible2 = false">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      active: 1,
      inactive: 2,
      dfUrl: 'http://localhost:10086/ps/',
      srcList: [],
      userlist: [],
      albumlist: [],
      photolist: []
    }
  },
  async created () {
    this.getuserlist()
  },
  methods: {
    async getuserlist () {
      const { data: res } = await this.$http.get('user/list')
      this.userlist = res.obj
    },
    async getalbumlist (item) {
      this.dialogVisible1 = true
      const id = item.userid
      const { data: res } = await this.$http.get('album/list', { params: { id } })
      this.albumlist = res.obj
      console.log(res)
    },
    async getphotolist (item) {
      this.dialogVisible2 = true
      const { albumid: id } = item
      const { data: res } = await this.$http.get('album/getphotobyid', { params: { id } })
      if (res.code === 200) {
        this.photolist = res.obj.photoList
        this.photolist.some(item => {
            this.srcList.push(this.dfUrl + item.photoaddress)
        })
      }
    },
    async statusChange (item) {
      console.log(item)
      if (item.albumid) {
        const { albumid: id, albumtype: ban } = item
        console.log(id, ban)

        const { data: res } = await this.$http.post('admin/banAlbum', { id, ban })
      } else if (item.photoid) {
        const { photoid: id, photoban: ban } = item
        console.log(id, ban)
        const { data: res } = await this.$http.post('admin/banPhoto', { id, ban })
      }
    },
    handleClose1 () {
      this.dialogVisible1 = false
    },
    handleClose2 () {
      this.dialogVisible2 = false
    }
  }
}
</script>
<style lang="less" scoped>
.albumlist {
  display: flex;
  list-style: none;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  margin-top: 20px;
  li {
    width: 174px;
    height: 220px;
    cursor: pointer;
    padding: 5px;
    margin-bottom: 10px;
    background-color: #fff;
    box-shadow: 1px 1px 10px #3332;
  }
  > li:not(:nth-child(7n)) {
    margin-right: 11px;
  }
}
.albumlist.albumlist1 {
  li {
    margin-right: 0;
  }
  > li:not(:nth-child(6n)) {
    margin-right: 13px;
  }
}
a:hover,
a:focus {
  text-decoration: none;
}
.albumlist-img {
  width: 100%;
  height: 164px;
  display: flex;
  justify-content: center;
  align-items: center;
  i {
    font-size: 40px;
  }
}
.albumlist-b {
  height: 46px;
  line-height: 46px;
  text-align: center;
}
.albumlist1-b {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
