<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">
        首页
      </el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table
        :data="userlist"
        stripe
        style="width: 100%"
      >
        <el-table-column label="用户状态">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.userban"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="active"
              :inactive-value="inactive"
              @change="statusChange(scope.row)"
            />
          </template>
        </el-table-column>
        <el-table-column
          prop="userid"
          label="用户ID"
        />
        <el-table-column
          prop="username"
          label="用户名"
        />
        <el-table-column
          prop="usersex"
          label="性别"
        />
        <el-table-column
          prop="userphone"
          label="手机"
        />
        <el-table-column
          prop="useremail"
          label="邮箱"
        />
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              type="warning"
              size="mini"
              icon="el-icon-edit"
              @click.stop="alter(scope.row)"
            />
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-dialog
      center
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      title="修改用户信息"
      width="35%"
    >
      <el-form
        inline
        label-width="65px"
        :rules="rules"
        ref="alteruserFormref"
        :model="alteruserForm"
      >
        <el-form-item
          prop="username"
          label="用户名"
        >
          <el-input v-model="alteruserForm.username" />
        </el-form-item>
        <el-form-item label="性别">
          <el-select
            v-model="alteruserForm.usersex"
            placeholder="请选择"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item
          prop="userphone"
          label="手机"
          disabled
        >
          <el-input v-model="alteruserForm.userphone" />
        </el-form-item>
        <el-form-item
          prop="useremail"
          label="邮箱"
        >
          <el-input v-model="alteruserForm.useremail" />
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submit('alteruserFormref')">提交</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    const validateemail = (rele, value, callback) => {
      const email = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
      if (email.test(value) || value == '' || value == undefined) {
        callback()
      } else {
        callback(new Error('请输入正确的邮箱'))
      }
    }
    const validatephone = (rele, value, callback) => {
      const phone = /^1[3456789]\d{9}$/
      if (phone.test(value) || value == '' || value == undefined) {
        callback()
      } else {
        callback(new Error('请输入正确的手机号码'))
      }
    }
    return {
      userlist: [{}],
      alteruserForm: {},
      dialogVisible: false,
      active: 1,
      inactive: 2,
      options: [
        { label: '男', value: 1 },
        { label: '女', value: 2 }
      ],
      rules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        userphone: [{ validator: validatephone, trigger: 'change' }],
        useremail: [{ validator: validateemail, trigger: 'change' }]
      }
    }
  },
  created () {
    this.getuserlist()
  },
  methods: {
    // 获取所有用户信息
    async getuserlist () {
      const { data: res } = await this.$http.get('user/list')
      this.userlist = res.obj
      console.log(this.userlist)
    },
    // 修改用户信息
    alter (row) {
      console.log(row)
      this.dialogVisible = true
      this.alteruserForm = row
    },
    // 提交修改
    submit (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        const { data: res } = await this.$http.post('user/updateUser', this.alteruserForm)
        console.log(res)
        if (res.code === 200) {
          this.userlist.some(item => {
            if (item.userid === this.alteruserForm.userid) {
              item = this.alteruserForm
            }
          })
          this.handleClose()
        }
      })
    },
    handleClose () {
      this.dialogVisible = false
    },
    // 用户状态改变
    async statusChange (row) {
      const { userid: id, userban: ban } = row
      console.log(id, ban);
      const { data: res } = await this.$http.post('admin/banUser', { id, ban })
    }
  }
}
</script>
